	onShow() {
		// 自提点
		uni.request({
			
			url: this.config.url + "member/station",
			method: "post",
			data: {
				token: this.token
			},
			success: (res) => {
			console.log(111111111)
				this.selfList = res.data.data;
				this.selfLen = res.data.data.length;
					console.log(res, res.data.data.length, "zi")
			}
		
		})
		//我的收货地址
		uni.request({
			url: this.config.url + "member/address",
			method: "POST",
			data: {
				token: this.token
			},
			success: (res) => {
				console.log(11111111221)
				console.log(res, res.data.data.length, "wo")
				this.homeList = res.data.data;
				this.homeLen = res.data.data.length;
			}
		})
	},
	mounted() {

		
	},
	
	data() {
		return {
			src1: '../../../static/img/address/y-duihao.png',
			src2: '../../../static/img/address/n-duihao.png',
			typeClass: 'home',
			subState: 1,
			isSelect: false,
			homeList: [],
			selfList: [],
			homeLen: "",
			selfLen: ""
		};
	},
	onLoad(e) {
		if (e.type == 'select') {
			this.isSelect = true;
			console.log(11111)
		}
	},
	onReady() {
		console.log("ready")
	},
	methods: {

		// 删除我的收货地址
		removeH(row, id) {
			var _this = this;
			uni.showModal({
				title: '提示',
				content: '确定要删除收货地址',

				cancelText: '取消',
				confirmText: '确认',
				confirmColor: "#14CC21",
				success: res => {
					var home = _this.homeList;
					console.log("hoem", home)
					this.homeList.splice(row, 1)
					uni.request({
						url: this.config.url + "address/del",
						data: {
							token: this.token,
							address_id: id,
						},
						method: "post",
						success: (res) => {
							if (res.data.code == 1) {

							}
						}
					});

				},

			});
		},

		// 删除自提点
		removeS(row, id) {
			uni.showModal({
				title: '提示',
				content: '确定要删除收货地址',

				cancelText: '取消',
				confirmText: '确认',
				confirmColor: "#14CC21",
				success: res => {

					uni.request({
						url: this.config.url + "station/del",
						data: {
							token: this.token,
							address_id: id,
						},
						method: "post",
						success: (res) => {
							if (res.data.code == 1) {
								var self = this.selfList;
								this.selfList = self.splice(row, 1)
							}
						}
					});

				},

			});
		},

		switchType(type) {

			this.typeClass = type;
			if (type == "home") {
				this.subState = 1;
				console.log("home",this.homeLen)

			} else if (type == "self") {
				this.subState = 2;
				console.log("se",this.selfLen)
			}
		},
		edit(row) {
			var type = JSON.stringify(row)

			uni.navigateTo({
				url: "/pages/user/address/edit/edit?type=" + type
			});

		},
		clear(row) {
			console.log(row)
		},
		add() {
			uni.navigateTo({
				url: "edit/edit"
			})
		},
		select(row) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			uni.setStorage({
				key: 'selectAddress',
				data: row,
				success() {
					uni.navigateBack();
				}
			})
		}
	}
}
